import React from 'react'
import './discover.css'
//apollo
import { useQuery } from '@apollo/client';
import { PROJECTS_QUERY } from './gql/queries';
//preloader
import Preloader from '../../preloader/Preloader';
import Post from '../../post/Post';
import { useSelector } from 'react-redux';
import Error401 from '../error/Error401';


function Discover() {
  let username = useSelector(state => state.user.username)
    //query hook
    const {data, loading, error} = useQuery(PROJECTS_QUERY);


  if(!username){
    return (<Error401 />)// if token is either JWT on blank go to throw error
  }

  //while loading
  if(loading) return <Preloader />
  //if we encounter an error
  if(error) return <div className='posts'><p> Error fetching data</p></div>
  //if fetching the data succeeds

  return (
    <div className='discover'>
      <div className="space" />
      {
        /* looping through the data to create posts */
        data.allProjects.map( (item) => <Post id={item.id} imageURL={item.image} title={item.title} description={item.description} /> )
      }
      <div className="space" />
    </div>
  )
}

export default Discover