import React from 'react'
import './news.css'

import { CommunitySidebar } from '../../community-sidebar/CommunitySidebar'
import { NewsPost } from '../../newsPost/NewsPost'
import { useQuery } from '@apollo/client'
import { ALLARTICLES_QUERY } from './gql/query'


export const News = () => {
  const {data, loading, error} = useQuery(ALLARTICLES_QUERY)


  return (
    <div className='news'>

      <div className="title-container">
          Articles
      </div>
      
      <div className="postContainer">
        {
          loading ? 'loading...' : ''
        }
        {
          error ? 'Error fetching latest Articles': ''
        }
        {
          data ? 
            data.allArticles.map((article) => <NewsPost key={article.id} id={article.id} title={article.title} imgURL={article.image} author={article.author.username} />)
          :
            ''
        }
      </div>
    </div>
  )
}
