import { useMutation, useQuery } from '@apollo/client';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import Preloader from '../../../preloader/Preloader';
import { PERSONAL_INFO_MUTATION } from './gql/mutation';
import { USERINFO_QUERY } from './gql/query';
import './profile.css'

export default function BankingInfo() {
    const navigate = useNavigate()

    const [userInfo, setUserInfo] = useState({
      bank:'',
      branchCode:'',
      branchName:'',
      accountNumber:''
    })

    const [metaData , setMetaData] = useState({
        notifyUser:'',
        loading:false,
        errors:''
    });

  
    const handleOnChange = (event) => {
      setUserInfo({...userInfo, [event.target.name]: event.target.value})
    }
  
    //apollo hooks
    const {data, loading, error} = useQuery(USERINFO_QUERY, {
        onCompleted: data => {data.me ? setUserInfo(data.me): navigate('/signin')} //if statement is to avoid cases like data = {me:null} 
    });

    const [mutateFunction] = useMutation(PERSONAL_INFO_MUTATION, {
        onError: error => setMetaData({...metaData, errors:`${error}`}),
        onCompleted: data =>{
            console.log('completed request')
            if(data.updateAccount.success){
                setMetaData({...metaData, notifyUser:'Sucessfully updated information'})
                navigate('/profile')
            }
            if(data.updateAccount.errors){
                setMetaData({...metaData, errors:`${JSON.stringify(data.updateAccount.errors)}`})
            }
        }
    });

    const onSave = (variablesObj) =>{
        console.log(variablesObj)
        setMetaData({...metaData, loading:true})
        mutateFunction(variablesObj)
    }
    
  
    if(loading){ return <Preloader />}
    if(error){ return <div>Error fetching user data</div>}
    if(data.me){
        //console.log(data.me)
        //setUserInfo(data.me)
      }

  
    return (
      <div className="profile">
          <div className="profileForm">
              <h2>Banking Information</h2>
              <div style={{color:'green'}}>
                {
                    metaData.notifyUser
                }
              </div>
              <div style={{color:'red'}}>
                {
                    metaData.errors
                }
              </div>

              <div className="formField">
                  <label htmlFor="bank">Bank Name</label>
                  <input 
                      name="bank" 
                      className='profileForm' 
                      type="text" 
                      placeholder='Bank Name'
                      value={userInfo.bank}
                      onChange={(e) => handleOnChange(e)}
                  />
              </div>
              <div className="formField">
                  <label htmlFor="branchCode">Branch Code</label>
                  <input 
                      name="branchCode" 
                      className='profileForm' 
                      type="text" 
                      placeholder='Branch Code'
                      value={userInfo.branchCode}
                      onChange={(e) => handleOnChange(e)}
                  />
              </div>
              <div className="formField">
                  <label htmlFor="branchName">Branch Name</label>
                  <input 
                      name="branchName" 
                      className='profileForm' 
                      type="text" 
                      placeholder='Branch Name'
                      value={userInfo.branchCode}
                      onChange={(e) => handleOnChange(e)}
                  />
              </div>
              <div className="formField">
                  <label htmlFor="accountNumber">Account Number</label>
                  <input 
                      name="accountNumber" 
                      className='profileForm' 
                      type="text" 
                      placeholder='Account Number'
                      value={userInfo.branchCode}
                      onChange={(e) => handleOnChange(e)}
                  />
              </div>

  
              <div className="formField">
                  <button
                    onClick={() => onSave({variables: userInfo})}
                  >
                    {
                        metaData.loading ? 'Processing':'Save'
                    }
                </button>
              </div>
     
          </div>
      </div>
      
    )
}








