function Detail(props) {
  let username = useSelector(state => state.user.username)

  const { id } = useParams();

  const [metaData , setMetaData] = useState({
    notifyUser:'',
    loading:false,
    errors:''
  });

  const [mutateFunction] = useMutation(NEWINVESTMENT_MUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{
        const storeData = async (data) => {
            try {
              //setJWT(JSON.stringify(data))
              if(data.newInvestment.portfolioObj.id){
                setMetaData({...metaData, notifyUser:'Investment Submitted Successfully, Check your Portfolio to confirm changes', errors:''})
                console.log(metaData.notifyUser)
              }else{
                setMetaData({...metaData, errors:`${JSON.stringify('Server Experienced an Error Processing your request')}`})
              }
            } catch (e) {
              // saving error
            }finally{
                
            }
          }

        storeData(data)


    }
  });

  const {data, loading, error} = useQuery(GETPROJECT_QUERY, {variables: {id}});

  //amount user may want to invest based on invest form
  let [amountToInvest, setAmoutToInvest] = useState(1);

  if(!username){
    return <Error401 /> // if token not found go to sigin page
  }
  //while loading
  if(loading) return <Preloader />
  //if we encounter an error
  if(error) return <div className='posts'><p> Error fetching data</p></div>


  const onSubtract = () =>{
    if(!(amountToInvest <= 1)){
        setAmoutToInvest(amountToInvest - 1)
    }
  }
  const onAdd = () =>{
    if(!(amountToInvest >= data.getProject.minimumInvestment)){
        setAmoutToInvest(amountToInvest + 1)
    }
  }
  const onInvestInputValueChange = (e) =>{
    const value = e.target.value;
    if(value >= data.getProject.minimumInvestment){
    }
    else if (value <= 1){
    }else{
        setAmoutToInvest(value)
    }
  }

  const handleInvest = () =>{
    mutateFunction({variables:{
        "investmentAmount": amountToInvest,
        "projectId": id,
        username
      }})
  }

  return (
    <div className='detail'>
        <div className='mainSection'>

            <img src={data.getProject.image} alt="Project" />
            
            <div className="postDetail"> 
              <h3>{data.getProject.title}</h3>

              {
                data.getProject.description
              }
            </div>
        </div>


        <div className='rightSideSection'>
            <div className="rightSideContent">
                <h3>Project Details</h3>
                <div className="rightSectionInfo">
                    <div className="infoRow">
                        <div className="rowText">Investment amount required:</div>
                        <div className='rowValue'><b>$ {data.getProject.capitalRequirement}</b></div>
                    </div>
                    <div className="infoRow">
                        <div className="rowText">Project End:</div>
                        <div className='rowValue'><b>{data.getProject.mature} </b></div>
                    </div>
                    <div className="infoRow">
                        <div className="rowText">Expect Project Returns:</div>
                        <div className='rowValue'><b>${data.getProject.returns} per dollar</b></div>
                    </div>

                    <div className="infoRow">
                        <div className="infoRowButtonSeries">
                            <div className="button" onClick={() => onAdd()}>+</div>
                            <div className="button">$
                                <input type="number" value={amountToInvest} onChange={(e) => onInvestInputValueChange(e)}/>
                            </div>
                            <div className='button' onClick={() => onSubtract()}>-</div>
                        </div>
                    </div>
                    <div className="infoRow">
                        <div className="rowText">Your investment amount:</div>
                        <div className='rowValue' style={{color:'rgb(4, 156, 4)'}}><b>${amountToInvest}</b></div>
                    </div>
                    <div className="infoRow">
                        <div className="investButton" onClick={() => handleInvest()}>Invest</div>
                    </div>

                    <div className="infoRow">
                        <div style={{color:'red'}}>
                            {
                                metaData.errors
                            }
                        </div>
                        <div style={{color:'green'}}>
                            {
                                metaData.notifyUser
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>  

        <div className="space" />     
    </div>
  )
}

export default Detail
