import React from 'react'
import { Link } from 'react-router-dom'
import './Post.css'
import PostFooter from './sub-components/PostFooter'
import PostImage from './sub-components/PostImage'

function Post({id, imageURL, title, description}) {
  return (
    <div className='Post'>
      <Link to={`/detail/${id}`} >
        <PostImage imageURL={imageURL} />
        <PostFooter title={title} description={description}/>
      </Link>
    </div>
  )
}

export default Post