import { gql } from "@apollo/client";


const NEWPROJECT_MUTATION = gql`
    mutation newProjectMutation($username:String!,$image:Upload!,$title:String!,$description:String!, $category:String, $capitalRequirement:Decimal!, $minimumInvestment:Decimal!, $mature:String!, $returns:Decimal!){
        newProject(username:$username, image:$image, title:$title,description:$description,category:$category, capitalRequirement:$capitalRequirement, minimumInvestment:$minimumInvestment, mature:$mature, returns:$returns){
            project{
                id
            }
        }
    }
`;

export {NEWPROJECT_MUTATION}