import { useMutation } from '@apollo/client';
import React, { useState } from 'react'
import { NEWPROJECT_MUTATION } from './gql/mutation';
import './createProject.css'
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import Error401 from '../error/Error401';

function CreateProject() {
  let username = useSelector(state => state.user.username)

  const [userInput, setUserInput ] = useState({
    image:'',
    title:'',
    username: username,
    description:'',
    category:'',
    mature:'',
    returns:'',
    capitalRequirement:'',
    minimumInvestment:''
  });

  const [metaData , setMetaData] = useState({
    notifyUser:'',
    loading:false,
    errors:''
  });

  const navigate = useNavigate()


  // calling the signin event on apollo client
  const [mutateFunction] = useMutation(NEWPROJECT_MUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{
        const storeData = async (data) => {
            try {
              //setJWT(JSON.stringify(data))
              
              if(data.newProject.project.id){
                setMetaData({...metaData, notifyUser:'Project Submitted Successfully '})
                setMetaData({...metaData, errors:''}) //clearing errors
                navigate(`/detail/${data.newProject.project.id}`)
              }else{
                setMetaData({...metaData, errors:`${JSON.stringify('Server Experienced an Error Processing your request')}`})
              }
            } catch (e) {
              // saving error
            }finally{
                
            }
          }


        storeData(data)


    }
  });

  if(!username){
    return (<Error401 />)// if token is either JWT on blank go to throw error
  }


  const onChange = (event) =>{
    try{
        if(event.target.files[0]){//checking whether a file was uploaded or not on the input
            console.log('file detected: ', event.target.files[0])
            setUserInput({...userInput, [event.target.name]: event.target.files[0]})
        }else{//else is not particularly necessary, just increases robustness of mechanism
            console.log('file detected but no assigned to state')
            setUserInput({...userInput, [event.target.name]: event.target.value}) 
        }
    }catch(e){
        console.log('no file detected in input')
        setUserInput({...userInput, [event.target.name]: event.target.value})
    }
        
  }


  const onUpload = () =>{
    setMetaData({...metaData, loading:true});
    mutateFunction({variables: userInput});//to make the useMutation function
  }


  return (
    <div className='createProject'>
        <h2>Create a New Project</h2>
        <div style={{color:'green'}}>
          {
            metaData.notifyUser
          }
        </div>
        <div style={{color:'red'}}>
          {
            metaData.errors
          }
        </div>
        <div className="createProjectInputForm">         
          <div className="formField">
            <input 
              name='image' 
              className='createProjectInput' 
              type="file" 
              required
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <input 
              name='title' 
              className='createProjectInput' 
              type="text" 
              placeholder='Project Title' 
              value={userInput.title}
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <textarea
              name='description' 
              className='createProjectInput' 
              style={{height:'200px'}} 
              type="text" 
              placeholder='Project Description' 
              value={userInput.description}
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <input 
              name='category' 
              className='createProjectInput' 
              type="text" 
              placeholder='Project Category' 
              value={userInput.category}
              onChange={(e) => onChange(e)}
            />
          </div>
          <div className="formField">
            <input 
              name='minimumInvestment' 
              className='createProjectInput' 
              type="number" 
              placeholder='Minimum amount an investor need to put in to invest' 
              value={userInput.minimumInvestment}
              onChange={(e) => onChange(e)}
            />
          </div>
          <div className="formField">
            <input 
              name='mature' 
              className='createProjectInput' 
              type="text" 
              placeholder='Project completion date e.g 22 October 2022' 
              value={userInput.mature}
              onChange={(e) => onChange(e)}
            />
          </div>
          <div className="formField">
            <input 
              name='returns' 
              className='createProjectInput' 
              type="number" 
              placeholder='Expected investor return per dollar e.g $1.60' 
              value={userInput.returns}
              onChange={(e) => onChange(e)}
            />
          </div>
          <div className="formField">
            <input 
              name='capitalRequirement' 
              className='createProjectInput' 
              type="number" 
              placeholder='Total capital (money) the project requires' 
              value={userInput.capitalRequirement}
              onChange={(e) => onChange(e)}
            />
          </div>
        
          <div className="formField">
            <button onClick={() => onUpload()} >Upload</button>
          </div>          
        </div>
    </div>
  )
}

export default CreateProject