import { useMutation } from '@apollo/client';
import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import setJWT from '../../../../utils/setJWT';
import './signin.css'
//redux 
import { useDispatch } from 'react-redux'; 
import authIsTrue from '../../../../redux_alpha_impl/actions/authIsTrue';
import { SIGNIN_MUTATION } from './gql/mutation';
import setUser from '../../../../redux_alpha_impl/actions/setUser';

function Signin({appAuthState, setAuthState}) {
  const dispatch = useDispatch()

  const [userInput, setUserInput] = useState({
    username:'',
    password:''
  });

  const [metaData , setMetaData] = useState({
    loading:false,
    errors:''
  });

  const navigate = useNavigate()


  // calling the signin event on apollo client
  const [mutateFunction] = useMutation(SIGNIN_MUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{
        const storeData = (data) => {
            try {
              //setJWT(JSON.stringify(data))
              
              if(data.tokenAuth.success){

                setJWT(JSON.stringify({...data.tokenAuth.user, token: data.tokenAuth.token}))
                console.log('making new app state')
                dispatch(authIsTrue())
                dispatch(setUser({...data.tokenAuth.user, token: data.tokenAuth.token})) //NB token is outside the user obj, so had to create a new object with them together
                console.log('redux must be now true')
                navigate('/')
              }
              else{
                setMetaData({...metaData, errors:'Invalid username or password'})
              }
            } catch (e) {
              // saving error
            }finally{
                
            }
          }


        storeData(data)


    }
  });

  //to handle Signin button click
  const handleSignin = (variablesObj) => {
      console.log('handle sigin clicked')
      if(userInput.username !== '' & userInput.password !== ''){
          setMetaData({...metaData, loading:true});
          mutateFunction(variablesObj);//to make the useMutation function
      }else{

      }
  }


  const onChange = (event) =>{
    setUserInput({...userInput, [event.target.name]: event.target.value})
  }

  return (
    <div className='signin'>
        <h2>Signin</h2>
        <div style={{color:'red'}}>
          {
            metaData.errors
          }
        </div>
        <div className="signinInputForm">
        <div className="formField">
            <input 
              name='username' 
              className='signinInput' 
              type="text" 
              placeholder='Username'
              value={userInput.username}
              onChange={(e) => onChange(e)} 
            />
          </div>

          <div className="formField">
            <input 
              name='password' 
              className='signinInput' 
              type="password" 
              placeholder='Password' 
              value={userInput.password}
              onChange={(e) => onChange(e)}
            />
          </div>
        
          <div className="formField">
            <button 
              onClick={() => handleSignin({variables: userInput})}
            >Signin</button>
            <Link to='/forgotpassword'>
              <div className="coloredText">Forgot Password</div>
            </Link>
          </div>

          <Link to='/register'>
            <div className="coloredText">Create an account</div>
          </Link>
        </div>
    </div>
  )
}

export default Signin