import { useEffect, useState } from 'react';
import getJWT from './utils/getJWT'
import {Routes, Route} from 'react-router-dom'
import { ApolloClient, InMemoryCache, ApolloProvider, gql } from '@apollo/client';
import {createUploadLink} from 'apollo-upload-client'

import { split, HttpLink } from '@apollo/client';
import { getMainDefinition } from '@apollo/client/utilities';
import { WebSocketLink } from '@apollo/client/link/ws'
import { SubscriptionClient } from 'subscriptions-transport-ws'

import './App.css';
import Header from './components/header/Header';
import Home from './components/pages/home/Home'
import Detail from './components/pages/detail/Detail';
import Footer from './components/footer/Footer';
import Discover from './components/pages/discover/Discover';
import Portfolio from './components/pages/portfolio/Portfolio';
import Profile from './components/pages/accounts/profile/Profile';
import Signin from './components/pages/accounts/signin/Signin';
import Register from './components/pages/accounts/register/Register';
import ForgotPassword from './components/pages/accounts/signin/ForgotPassword';
import PersonalInfo from './components/pages/accounts/profile/PersonalInfo';
import BankingInfo from './components/pages/accounts/profile/BankingInfo';
import ChangePassword from './components/pages/accounts/profile/ChangePassword';
import { useDispatch, useSelector } from 'react-redux';
import CreateProject from './components/pages/createProject/CreateProject';
import setUser from './redux_alpha_impl/actions/setUser';
import { News } from './components/pages/news/News';
import { NewsDetail } from './components/pages/newsDetail/NewsDetail';
import { Chats } from './components/pages/chats/Chats';
import { MsgPanel } from './components/msgPanel/MsgPanel';
import { CommunitySidebar } from './components/community-sidebar/CommunitySidebar';
import Verify from './components/pages/accounts/verify/Verify';

const USERINFO_QUERY = gql`
    query userInfo{
        me{
            username
            email
                firstName
            lastName
            
        }
    }
`;

export {USERINFO_QUERY}
function App() { 
  //const [authorization, setAuthorization] = useState('')
  const user = useSelector(state => state.user)
  let dispatch = useDispatch()

  useEffect(() =>{
    //asyn function to retrieving the jwt from local storage

    try {
      const value = getJWT();
      console.log('localstorage: ',value)
      console.log('redux: ', user)
      console.log('condition', !user.username && value.username )
      if(!user.username && value.username ) {//to prevent a runaway situation
        // setting the authorization token
        dispatch(setUser(value))
      }else{
        //if no toke is found
        console.log('no token found : ',value);
      }
    } catch(e) {
      // error reading value
    }

  });
  
  
  
  
  const httpLink = createUploadLink({
    uri: 'http://ec2-54-167-127-129.compute-1.amazonaws.com:8001/graphql/',
    headers: {
      authorization: user.token ? `JWT ${user.token}`: ''
    }
  });
  
  const wsLink = new WebSocketLink(
    new SubscriptionClient('ws://ec2-54-167-127-129.compute-1.amazonaws.com:8001/ws/graphql', {
      connectionParams: {
        authToken: user.token ? `JWT ${user.token}`: ''
      }
    })
  );
  
  // The split function takes three parameters:
  //
  // * A function that's called for each operation to execute
  // * The Link to use for an operation if the function returns a "truthy" value
  // * The Link to use for an operation if the function returns a "falsy" value
  const splitLink = split(
    ({ query }) => {
      const definition = getMainDefinition(query);
      return (
        definition.kind === 'OperationDefinition' &&
        definition.operation === 'subscription'
      );
    },
    wsLink,
    httpLink,
  );  
  //configuring API uri and cache

  
  const client = new ApolloClient({
    link: splitLink,
    cache: new InMemoryCache(),
  });

  return (
    <ApolloProvider client={client}>
      <div className="App">
        <Header/>
        <CommunitySidebar />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/detail/:id" element={<Detail />} />
          <Route path="/discover" element={<Discover />} />
          <Route path="/portfolio" element={<Portfolio />} />
          <Route path="/signin" element={<Signin />} /> {/*passing auth state so that there is an app wide refresh updating user jwt on signin */}
          <Route path="/forgotpassword" element={<ForgotPassword />} />
          <Route path="/register" element={<Register />} />
          <Route path="/verify" element={<Verify />} />
          <Route path="/profile" element={<Profile />} />
          <Route path="/profile/personal" element={<PersonalInfo />} />
          <Route path="/banking" element={<BankingInfo />} />
          <Route path="/changePassword" element={<ChangePassword />} />
          <Route path="/createProject" element={<CreateProject />} />
          {/* people section */}
          <Route path="/articles" element={<News />} />
          <Route path="/article/detail/:id" element={<NewsDetail />} />
          <Route path="/chats" element={<Chats />} />
          <Route path="/chat/o/:id" element={<MsgPanel />} />
        </Routes>
        <Footer />
      </div>
    </ApolloProvider>
  );
}

export default App;
