import { useMutation } from '@apollo/client';
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import setJWT from '../../../../utils/setJWT';
import './verify.css'

import { RESENDACTIVATIONEMAIL_MUTATION, VERIFY_MUTATION } from './gql/mutation';

//redux 
import { useDispatch } from 'react-redux'; 


function Verify({appAuthState, setAuthState}) {
  const [tokenEmailToggle, setTokenEmailToggle] = useState(false) //if tokenEmailToggle=true show verify token form
  const [token, setToken] = useState('');
  const [email, setEmail] = useState('')
  const [metaData , setMetaData] = useState({
    loading:false,
    errors:''
  });
  const navigate = useNavigate()

  // calling the verify event on apollo client
  const [mutateFunction] = useMutation(VERIFY_MUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{console.log(data)}
  });
  
  const [resendEmailMutateFunction] = useMutation(RESENDACTIVATIONEMAIL_MUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{console.log(data)}
  });


  return (
    <div className='verify'>
        <h2>{tokenEmailToggle ? 'Verify Token':'Resend Email'}</h2>
        <div style={{color:'red'}}>
          {
            metaData.errors
          }
        </div>
        <div className="verifyInputForm">
        <div className="formField">
            <input 
              name='token' 
              className={tokenEmailToggle ? 'verifyInput':'verifyInput inactive'}
              type="text" 
              placeholder='Your verification token'
              value={token}
              onChange={(e) => setToken(e.target.value)} 
            />
            <input 
              name='token' 
              className={tokenEmailToggle ? 'verifyInput inactive':'verifyInput'}
              type="text" 
              placeholder='Enter your Email'
              value={email}
              onChange={(e) => setEmail(e.target.value)} 
            />
          </div>
        
          <div className="formField">
            <button 
              className={tokenEmailToggle ? '':'inactive'}
              onClick={() => mutateFunction({variables: {token} })}
            >verify</button>
            <button 
              className={tokenEmailToggle ? 'inactive':''}
              onClick={() => resendEmailMutateFunction({variables: {email} })}
            >Resend Email</button>


            <div className={tokenEmailToggle ? 'coloredText inactive':"coloredText"} onClick={() => setTokenEmailToggle(true)}>Verify Token</div>
            <div className={tokenEmailToggle ? 'coloredText':'coloredText inactive'} onClick={() => setTokenEmailToggle(false)}>Resend Email</div>
          </div>
        </div>
    </div>
  )
}

export default Verify