import React from 'react'
import Diversity3Icon from '@mui/icons-material/Diversity3';
import { ChatTile } from '../../chatTile/ChatTile'
import './chats.css'
import { useQuery } from '@apollo/client'
import { USER_CHATS } from './gql/query'


export const Chats = () => {
   const { data, loading, error } = useQuery(USER_CHATS);
  
  const getParticipants = (participants) => {
    return participants.filter(participant => {
      // if (props.user && parseInt(participant.id) !== props.user.pk) {
      //   return participant
      // }
      return participant
    })
  }

  return (
      <div className='chats'>
          <div className="container">

            <div className="container-title">
              Your Networks
            </div>

            <div className="chats-list">
              {
                loading ? 'loading...': ''
              }
              {
                data &&
                data.chats.edges.map( chatObj =>{
                  try{
                    return(<ChatTile key={chatObj.node.id} id={chatObj.node.id} chatName={chatObj.node.name} profileURL={'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_960_720.png'} lastMsg={`${chatObj.node.messages.edges[0] ? chatObj.node.messages.edges[0].node.text : ''}`} lastMsgUser={':-'} />)
                  }catch(e){
                    console.error(e)
                  }
                })
              }
            </div>
            
          </div>
      </div>

  )
}
