import React, { useState } from 'react'
import { useMutation } from '@apollo/client';
import { RESETEMAILMUTATION } from './gql/mutation';
import './signin.css'

function ForgotPassword() {
    const [metaData, setMetaData] = useState({
        success:false,
        loading:false,
        errors:'',
    });
    const [email, setEmail] = useState('');

    // calling the signin event on apollo client
    const [mutateFunction] = useMutation(RESETEMAILMUTATION, {
        onError: error => {
            setMetaData({...metaData, loading:false});//setting loading to false
            setMetaData({...metaData, errors:`${error}`})
        },
        onCompleted: data => {
            setMetaData({...metaData, loading:false});//setting loading to false
            console.log('set loading to ', metaData.loading)

            if(data.sendPasswordResetEmail.success){
                setMetaData({...metaData, success:data.sendPasswordResetEmail.success });
                setMetaData({...metaData, errors:''});//clearing all errors
            }else{
                setMetaData({...metaData, errors:`${JSON.stringify(data.sendPasswordResetEmail.errors)}` });
            }
        }
    });

    const onChange = (event) =>{
        setEmail(`${event.target.value}`)
    }

    const handleReset = (variablesObj) =>{
        if(email !== '' ){
            setMetaData({...metaData, loading:true});
            mutateFunction(variablesObj);//to make the useMutation function
        }else{
            setMetaData({...metaData, errors:`Please enter a valid email address`})
        }
    }

    return (
        <div className='signin'>
            <h2>Password Reset</h2>
            <div style={{color:'green'}}>
            {
                metaData.success && ("Check your submited email for more instructions")            
            }
            </div>
            <div style={{color:'red'}}>
            {
                metaData.errors && (`${JSON.stringify(metaData.errors)}`)
            }
            </div>
            <div className="signinInputForm">
            <div className="formField">
                <input 
                className='signinInput' 
                type="text" 
                placeholder='Enter your Email address'
                value={email}
                onChange={(e) => onChange(e)} 
                />
            </div>

            
            <div className="formField">
                <button 
                    onClick={() => handleReset({variables: {email}})}
                >
                {
                    metaData.loading ?
                        'loading'
                    :
                        'Processing... '
                }
                </button>
            </div>

            </div>
        </div>
    )
}

export default ForgotPassword