import React, { useState } from 'react'
import './Header.css'
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import { gql, useQuery } from '@apollo/client';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import Sidebar from '../sidebar/Sidebar';
import Search from '../search/Search';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import Filters from '../filters/Filters';



const GET_METADATA_QUERY = gql`
  query get_meta {
    constance{
      siteTitle
      siteLogo
      siteDescription
    }
  }
`;

function Header() {
  //current url 
  const navigate = useNavigate()
  let path = useLocation();
  path = path.pathname
  //fetching data
  const {data, loading, error} = useQuery(GET_METADATA_QUERY);

  const [sideBarState, setSidebarState] = useState(false);
  const [searchFormState, setSearchFormState] = useState(false);
  const [headerFilterState, setHeaderFilterState] = useState(false);

  //while loading
  // if(loading) return <div className='posts'><p>loading</p></div>
  //if we encounter an error
  if(error) {
    if(`${error}` === 'Error: Signature has expired') navigate('/signin')
    return <div className='posts'><h3 style={{color:'rgb(26, 185, 26)'}}>{`${error}`} </h3></div>
  }
  //if fetching the data succeeds



  const onMenuClick = () =>{
    // setting other states to false
    setSearchFormState(false);
    setHeaderFilterState(false);
    sideBarState ? setSidebarState(false): setSidebarState(true)
  }
  const onSearchClick = () =>{
    // setting other states to false
    setSidebarState(false);
    setHeaderFilterState(false);
    searchFormState ? setSearchFormState(false): setSearchFormState(true)
  }
  const onArrowDownClick = () =>{
    // setting other states to false
    setSidebarState(false);
    setSearchFormState(false);
    headerFilterState ? setHeaderFilterState(false): setHeaderFilterState(true)
  }

  return (
    <div className={path === '/chats' || path === '/forums' || path === '/articles' || path.includes('/o/') || path.includes('news/detail') ? 'headerContainer outOfView' : 'headerContainer'}>
      <div className='header'>
          <div className='headerLeft' >
            {
              sideBarState ? 
                <div className='headerButtonClicked' onClick={() => onMenuClick()}>
                  <MenuIcon />
                </div>
              :
                <div className='headerButton' onClick={() => onMenuClick()}>
                  <MenuIcon />
                </div>
            }
            <Link style={{textDecoration: 'none'}} to={'/'} >
              {
                loading ? 
                  <div className='greyBar' />
                : 
                  <div className='siteName'>
                    {
                      data.constance.siteTitle
                    }
                  </div>
              }
            </Link>
          </div>
          
          <div className='headerRight'>
            {
              searchFormState ? 
                <div className='headerButtonClicked' onClick={() => onSearchClick()}>
                  <SearchIcon />
                </div>
              :
                <div className='headerButton' onClick={() => onSearchClick()}>
                  <SearchIcon />
                </div>
            }
            {
              headerFilterState ?
                <div 
                  className='headerButtonClicked dropdown' 
                  style={path === '/discover' ? {display: 'block'} : {}} //to make this component display only in discover page
                  onClick={() => onArrowDownClick()}
                >
                  <KeyboardDoubleArrowDownIcon />
                </div>
              :
                <div 
                  className='headerButton dropdown' 
                  style={path === '/discover' ? {display: 'block'} : {}} //to make this component display only in discover page
                  onClick={() => onArrowDownClick()}
                >
                  <KeyboardDoubleArrowDownIcon />
                </div>
            }
            
            
          </div>
      </div>
      <Sidebar toggleState={sideBarState} setToggleState={setSidebarState} />
      <Search toggleState={searchFormState} />
      {
        path === '/discover' ?
        <Filters displayState={headerFilterState} />
        :
        ''
      }
    </div>
  )
}

export default Header