const { gql } = require("@apollo/client");

const NEWINVESTMENT_MUTATION = gql`
    mutation newInvestmentMutation($investmentAmount:Decimal!, $projectId:ID!, $username:String!){
    newInvestment(investmentAmount:$investmentAmount,projectId:$projectId,username:$username){
        portfolioObj{
            id
            project{
                id
                title
            }
            amountInvested
            }
        }

    }
`;

export {NEWINVESTMENT_MUTATION}