import React from 'react'
import './Home.css'
import Post from '../../post/Post'
// slider
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
//apollo
import { gql, useQuery } from '@apollo/client';
// icons
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
//preloader
import Preloader from '../../preloader/Preloader';





//query to get info on our graphql server
const GET_POSTS = gql`
  query Home {

    constance {
      websiteAbout
    }

    allProjects {
      id
      title
      image
      description
    }
  } 
`;

export default function Posts() {
  //query hook
  const {data, loading, error} = useQuery(GET_POSTS);

  //while loading
  if(loading) return <Preloader />
  //if we encounter an error
  if(error) return <div className='posts'><p> Error fetching data</p></div>
  //if fetching the data succeeds

  const slider_Settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,
    initialSlide: 0,
    nextArrow: <ArrowForwardIcon />,
    prevArrow: <ArrowBackIcon  />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 3,
          dots: false,
          infinite: false,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  }; 
  // breakpoints is to add responsitivity to the slider


  return (
    <div className='home'>
      <div className='banner'>
        <div className='typewriter banner_text'> <h3>Your classified for aggro projects</h3> </div>
      </div>

      <div className='siteSummary'>
        {
          data.constance.websiteAbout
        }
      </div>

      <div className='sectionContainer'>
        <div className='sectionTop'>
          <h2>Latest Projects: </h2> 
          <div className='moreItemsButton'>
            More Items...
            <ArrowForwardIcon />
          </div>
        </div>
        <div className='item_list'>
          <Slider {...slider_Settings}>
            {
              /* looping through the data to create posts */
              data.allProjects.map( (item) => <Post id={item.id} imageURL={item.image} title={item.title} description={item.description} /> )
            }           
          </Slider>
        </div>
      </div>

      <div className='sectionContainer'>
        <div className='sectionTop'>
          <h2>Popular Projects: </h2> 
          <div className='moreItemsButton'>
            More Items...
            <ArrowForwardIcon />
          </div>
        </div>
        <div className='item_list'>
          <Slider {...slider_Settings}>
            {
              data.allProjects.map( (item) => <Post id={item.id} imageURL={item.image} title={item.title} description={item.description} /> )
            }
          </Slider>
        </div>
      </div>
      <div className="space" />
    </div>
  )
}