import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import AddIcon from '@mui/icons-material/Add';
import LogoutIcon from '@mui/icons-material/Logout';
import BarChartIcon from '@mui/icons-material/BarChart';
import PersonIcon from '@mui/icons-material/Person';
import setJWT from '../../utils/setJWT'
import './Sidebar.css'
import setUser from '../../redux_alpha_impl/actions/setUser';

export default function Sidebar(props) {
  let user = useSelector(state => state.user)
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const handlClick = (path) => {
    props.setToggleState(false)
    navigate(path)
  }

  const onLogout = () =>{
    props.setToggleState(false)
    setJWT({})
    dispatch(setUser({
      username:'',
      firstName:'', 
      lastName:'',
      email:'', 
      verified:'', 
      businessStatus:'',
      token:''
    }))
    navigate('/signin')
  }  

  return (
    <>
      <div className={`sidebar ${props.toggleState === true ? 'active' : ''}`}>
          <div className="sidebarBody">
            {
              user.businessStatus ? 
              <div className='sidebarOption' onClick={() => handlClick('/createProject')}>
                <AddIcon /> 
                <span>New Project</span>
              </div>
              :
              ''
            }

            {
              user.token ? 
              <div className='sidebarOption' onClick={() => handlClick('/profile')}>
                <PersonIcon />
                <span>Account</span>
              </div>
              :
              ''
            }
            {
              user.verified ? 
              ''
              :
              <div className='sidebarOption' onClick={() => handlClick('/verify')}>
                <PersonIcon />
                <span>Verify Account</span>
              </div>
            }

            {
              user.token ? 
              <div 
                className='sidebarOption logout' 
                onClick={() => onLogout()}
              >
                <LogoutIcon />
                <span>Log Out</span>
              </div>
              :
              <div className='sidebarOption' onClick={() => handlClick('/signin')}>
                <PersonIcon />
                <span>Sign in</span>
              </div>
            }
          </div>

          <div className="copyright">
            {/* Powered by: <div className="colouredText">NeutroSpot</div>  */}
            Licence and Terms of Service
          </div>
        </div> 
    </>
  )
}
