import { gql } from "@apollo/client";

export const USER_CHATS = gql`query getChats($last: Int){
    chats(last:$last) {
      edges {
        node {
          id
          group
          name
          lastModified
          participants {
            id
            username
            email
          }
          messages(last:1) {
            edges {
              node {
                id
                text
              }
            }
          }
        }
      }
    }
  }`
