import { gql } from "@apollo/client";


const RESENDACTIVATIONEMAIL_MUTATION = gql`
    mutation e($email:String!){
        resendActivationEmail(email:$email){
            success
            errors
        }
    }
`;

const VERIFY_MUTATION = gql`
    mutation verifyToken_Mutation($token:String!){
        verifyAccount(token:$token){
            success
            errors
        }
    }
`;

export {RESENDACTIVATIONEMAIL_MUTATION, VERIFY_MUTATION}