import React from 'react'
import './Search.css'

export default function Search({toggleState, onToggle}) {
  return (
    <div className={toggleState ? 'search active': 'search'}>
        <input className='searchInput' type="text" placeholder='search on the platform......' />
        <button className="searchButton" onClick={onToggle}>Search</button>
    </div>
  )
}
