import React, { useState } from 'react'
import './msgPanel.css'

import MoreVertIcon from '@mui/icons-material/MoreVert';
import AddReactionIcon from '@mui/icons-material/AddReaction';
import GifIcon from '@mui/icons-material/Gif';
import AttachmentIcon from '@mui/icons-material/Attachment';
import SendIcon from '@mui/icons-material/Send';
import { MsgThread } from './components/msgThread/MsgThread';
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import { useNavigate, useParams } from 'react-router-dom';
import { useMutation, useQuery } from '@apollo/client';
import { MESSAGES_QUERY } from './gql/query';
import { SUBSCRIBE_MESSAGES } from './gql/subscription';
import { CREATE_MESSAGE_MUTATION } from './gql/mutation';
import { useSelector } from 'react-redux';
import getJWT from '../../utils/getJWT';

export const MsgPanel = () => {
  const user = getJWT() // for username we'll use in sendMsg
  const navigate = useNavigate() //for back button next to profile
  let { id } = useParams(); //chatId from url
  const [message, setMessage] = useState('') //message text in input

  
  const { data, loading, error, subscribeToMore } = useQuery(MESSAGES_QUERY, {variables: {id, last:5 }}); //to retrieve latest 5 messages
  
  subscribeToMore({ //to keep watch of the latest message updates in the chat
    document: SUBSCRIBE_MESSAGES,
    variables: {username: user.username ? user.username : ''},
    updateQuery: (prev, { subscriptionData }) => {
      if (!subscriptionData.data) return prev;
      
      const newFeedItem = Object.assign({}, subscriptionData.data, {
        node:{
           ...subscriptionData.data 
        }
      }); /*this is to make the new subscription message have a structure similar to that of extisting messages, by adding node keyword to dictionary */

      return Object.assign({}, prev, {
        messages: {
          edges: [newFeedItem.onNewMessage, ...prev.messages.edges]
        }
      });
    }
  })

  const [mutateFunction] = useMutation(CREATE_MESSAGE_MUTATION, { //to send a new message
    onError: error => console.error(error),
  });

  if(loading) return(<div>loading...</div>) //while retrieving new messages

  return (
    <div className='msgPanel'>
        <div className="top">
            <div className="topPanelLeft" onClick={ () => navigate(-1)}>
                <KeyboardBackspaceIcon/>
            </div>
            <div className="topPanelMiddle"> 
                <img src={'https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_960_720.png'} alt="profile"/>
                <div className="info">
                    {data.chat.name} 
                </div>
            </div>
            <div className="topPanelRight"> <MoreVertIcon /> </div>
        </div>
        <div className="middle">
            <div className="msg-container">
                {
                    data && data.messages.edges.map((msg) => <MsgThread key={msg.node.id} msg={msg.node} />)
                }
                {
                    error && 'error fetching messages'
                }
            </div>
        </div>
        <div className="bottom">
            <div className="panel">
                <div className="emoji-toggle"><AddReactionIcon /></div>
                <div className="gif-sticker-toggle"><GifIcon /></div>
                <input className="text-input" placeholder='Message' value={message} onChange={(e) => setMessage(e.target.value)}></input>
                <div className="attach-toggle" ><AttachmentIcon /></div>
            </div>
            <div className="send-btn" onClick={() => { mutateFunction({variables: {chatId: id, message}}); setMessage(''); }}><SendIcon /></div>
        </div>
    </div>
  )
}
