import { gql } from "@apollo/client";

export const MESSAGES_QUERY = gql`query($id: ID,$last: Int){
    chat(id:$id){
    	name
    	group
    	participants{
        id
        username
        
      }    	
  	}
    
    messages(id:$id, last:$last) {
      edges {
        node {
          id
          text
          created
          read
          sender {
            id
            username
          }
        }
      }
    }
  }`;
