import { useQuery } from '@apollo/client'
import React from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import Preloader from '../../preloader/Preloader'
import Error401 from '../error/Error401'
import { GETPORTFOLIO_QUERY } from './gql/query'
import './portfolio.css'
import PortfolioProject from './PortfolioProject'



function Portfolio() {
    let username = useSelector(state => state.user.username)

    const {data, loading, error} = useQuery(GETPORTFOLIO_QUERY, {variables:{username}});

    if(!username){
      return (<Error401 />)// if token is either JWT on blank go to throw error
    }

    //while loading
    if(loading) return <Preloader />
    //if we encounter an error
    if(error) return <div className='posts'><p> Error fetching data</p></div>
    //if fetching the data succeeds

  return (
    <div className='portfolio'>
      <h2>Your Portfolio</h2>
      <div className="portfolioUpperSection">

        <div className="columns">
          <div className="column">
            <div className="row">
              Cash Balance: <div className="boldText">$100</div>
            </div>
            <div className="row">
              Portfolio value: <div className="boldText">$2500</div>
            </div>
          </div>

          <div className="column">
            <div className="row">
              Pending cash: <div className="boldText">$120</div>
            </div>
            <div className="row">
              Cleared cash: <div className="boldText">$100</div>
            </div>
          </div>
        </div>

        <div className="row">
          Total value: <div className="boldText">$2500</div>
        </div>

      </div>

      <div className="portfolioLowerSection">
        <div className="portfolioLowerSectionHeaders">
          <div className="portfolioLowerSectionHeader">Project</div>
          <div className="portfolioLowerSectionHeader">Invested</div>
          <div className="portfolioLowerSectionHeader">Mature Date</div>
        </div>
        <div className="portfolioProjects">
          {
            data.getPortfolio.map(item => {
              return(
                <Link to={`/detail/${item.project.id}`}>
                  <PortfolioProject key={item.id} title={item.project.title} amountInvested={item.amountInvested} mature={item.project.mature} />
                </Link>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Portfolio