import { gql } from "@apollo/client";

export const SUBSCRIBE_MESSAGES = gql`subscription($username: String) {
    onNewMessage(chatroom: $username) {
      message {
        chatSet {
          edges {
            node {
              id
            }
          }
        }
        sender{
          id
        }
        id
        text
        created
        read
      }
    }
  }`;
