import React from 'react'
import './communitySidebar.css'

import NewspaperIcon from '@mui/icons-material/Newspaper';
import Diversity3Icon from '@mui/icons-material/Diversity3';
import NotificationsActiveIcon from '@mui/icons-material/NotificationsActive';
import ChatIcon from '@mui/icons-material/Chat';
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import { Link, useLocation, useNavigate } from 'react-router-dom';

export const CommunitySidebar = () => {
  let path  = useLocation(); //url parameters 
  path = path.pathname
  const navigate = useNavigate()

  return (
    <div className={path === '/chats' || path === '/forums' || path === '/articles' ? 'community-sidebar active' : 'community-sidebar'}>
 
      <div className="community-sidebar-btn" onClick={() => navigate(-1)}>
        <div className="btn-icon">
          <KeyboardBackspaceIcon />
        </div>
      </div>

      <Link to='/articles'>
        <div className="community-sidebar-btn">
          <div className="btn-icon">
            <NewspaperIcon />
          </div>
        </div>
      </Link>
      
      {/* <Link to='/forums'>
        <div className="community-sidebar-btn">
          <div className="btn-icon">
            <Diversity3Icon />
          </div>
        </div>
      </Link> */}

      <Link to='/chats'>
        <div className="community-sidebar-btn">
          <div className="btn-icon">
            
            <Diversity3Icon />
          </div>
        </div>
      </Link>
    </div>
  )
}
