import { useMutation } from '@apollo/client';
import React, { useState } from 'react'
import { REGISTERMUTATION } from './gql/mutation';
import './register.css'

function Register() {
  const [userInput, setUserInput ] = useState({
    email:'',
    username:'',
    password1:'',
    password2:''
  });

  const [metaData , setMetaData] = useState({
    notifyUser:'',
    loading:false,
    errors:''
  });


  // calling the signin event on apollo client
  const [mutateFunction] = useMutation(REGISTERMUTATION, {
    onError: error => setMetaData({...metaData, errors:`${error}`}),
    onCompleted: data =>{
        const storeData = async (data) => {
            try {
              //setJWT(JSON.stringify(data))
              
              if(data.register.success && !data.register.errors){
                setMetaData({...metaData, notifyUser:'Registration Succesful, please check your email to activate your account'})
              }

              if(data.register.errors){
                setMetaData({...metaData, errors:`${JSON.stringify(data.register.errors)}`})
              }
            } catch (e) {
              // saving error
            }finally{
                
            }
          }


        storeData(data)


    }
  });


  const onChange = (event) =>{
    setUserInput({...userInput, [event.target.name]: event.target.value})
  }


  const onRegister = (variablesObj) =>{
    if(userInput.username !== '' & userInput.password !== ''){
      if(userInput.password1 === userInput.password2) {
        setMetaData({...metaData, loading:true});
        mutateFunction(variablesObj);//to make the useMutation function
      }else{
        setMetaData({...metaData, errors:'Your passwords do not match'})
      }
    }else{
      setMetaData({...metaData, errors:'Missing input fields, make sure you fill all forms'})
    }
  }


  return (
    <div className='register'>
        <h2>Register</h2>
        <div style={{color:'green'}}>
          {
            metaData.notifyUser
          }
        </div>
        <div style={{color:'red'}}>
          {
            metaData.errors
          }
        </div>
        <div className="registerInputForm">         
          <div className="formField">
            <input 
              name='email' 
              className='registerInput' 
              type="email" 
              placeholder='Email' 
              value={userInput.email}
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <input 
              name='username' 
              className='registerInput' 
              type="text" 
              placeholder='Username' 
              value={userInput.username}
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <input 
              name='password1' 
              className='registerInput' 
              type="password" 
              placeholder='Password' 
              value={userInput.password1}
              onChange={(e) => onChange(e)}
            />
          </div>

          <div className="formField">
            <input 
              name='password2' 
              className='registerInput' 
              type="password" 
              placeholder='Confirm Password' 
              value={userInput.password2}
              onChange={(e) => onChange(e)}
            />
          </div>
        
          <div className="formField">
            <button onClick={() => onRegister({variables: userInput})} >Register</button>
          </div>          
        </div>
    </div>
  )
}

export default Register