import React from 'react'
import { useSelector } from 'react-redux'
import './msgThread.css'

export const MsgThread = ({msg}) => {
  const user = useSelector(state => state.user)
  return (
    <div className={msg.sender.username === user.username ? 'msgThread me': 'msgThread' }>
        <div className="msgUser">{msg.sender.id !== 1 && (msg.sender.username) }</div>
        <div className="msg">{msg.text}</div>
        <div className="meta">{msg.time}</div>
    </div>
  )
}
