import { useQuery } from '@apollo/client';
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom'
import authIsFalse from '../../../../redux_alpha_impl/actions/authIsFalse';
import setUser from '../../../../redux_alpha_impl/actions/setUser';
import setJWT from '../../../../utils/setJWT';
import Preloader from '../../../preloader/Preloader';
import Error401 from '../../error/Error401';
import { USERINFO_QUERY } from './gql/query';
import './profile.css'

export default function Profile() {
  let user = useSelector(state => state.user)
  const navigate = useNavigate()
  const dispatch = useDispatch()

  if(!user.username){
    return (<Error401 />)// if token is either JWT on blank go to throw error
  }


  const onLogout = () =>{
    setJWT({})
    dispatch(setUser({}))
    navigate('/signin')
  }  


  return (
    <div className="profile">
        <div className="profileImg">
            <img src='https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_960_720.png' alt='profile' />
        </div>
        <div className="profileForm">
            <h2>Account</h2>

            <div className="formField">
                <label htmlFor="username">Username</label>
                <input 
                    name="username" 
                    className='profileForm' 
                    type="text" 
                    placeholder='Username'
                    value={user.username}
                    onChange={(e) => {}}
                />
            </div>
            <div className="formField">
                <label htmlFor="email">Email</label>
                <input 
                    name="email" 
                    className='profileForm' 
                    type="text" 
                    placeholder='Email'
                    value={user.email}
                    onChange={(e) => {}}
                />
            </div>

            <div className="formField">
                <Link to='/profile/personal'>
                    <div className="coloredText">Personal Information</div>
                </Link>
            </div>

            <div className="formField">
                <Link to='/banking'>
                    <div className="coloredText">Banking Information</div>
                </Link>
            </div>
            
            <div className="formField">
                <Link to='/changePassword'>
                    <div className="coloredText">Change Password</div>
                </Link>
            </div>

            <div className="formField">
                <button 
                    style={{backgroundColor:'rgb(211, 13, 13)', 
                    marginLeft:'0px'}} 
                    onClick={() => onLogout()}
                >logout</button>  
            </div>    
        </div>
    </div>
    
  )
}
