import React, { useState } from 'react';
import {Link } from "react-router-dom";
import { useLocation } from 'react-router';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import QueryStatsIcon from '@mui/icons-material/QueryStats';
import PeopleIcon from '@mui/icons-material/People';
import HomeIcon from '@mui/icons-material/Home';
import HubIcon from '@mui/icons-material/Hub';
import './footer.css'
import getJWT from '../../utils/getJWT';
import { useSelector } from 'react-redux';

export default function Footer() {
  const [active, setActive] = useState(false) //this sets whether the footer appears or not

  let path  = useLocation(); //url parameters 
  path = path.pathname

  let username = useSelector(state => state.user.username) 

  active && (setTimeout(() => setActive(false), 5000))
  return (
    <>
        <div className={active ? "footer-activate-btn" : "footer-activate-btn active" } onClick={() => setActive(!active)} onMouseEnter={() => setActive(!active)}/>

        <div className={active ? 'footer active': 'footer'} onMouseMove={() => setActive(true)}>
            <div className={ path === '/' ? 'footerOption active' : 'footerOption'}>
                <Link to='/' style={{textDecoration:'none', color:'inherit'}}>
                    <HomeIcon />
                    <div>Home</div>
                </Link>
            </div>
            <div className={ path === '/discover' ? 'footerOption active' : 'footerOption'}>
                <Link to={ username ? '/discover' : '/signin'} style={{textDecoration:'none', color:'inherit'}}>
                    <QueryStatsIcon />
                    <div>Discover</div>
                </Link>
            </div>
            <div className={ path === '/portfolio' ? 'footerOption active' : 'footerOption'}>
                <Link to={ username ? '/portfolio' : '/signin'} style={{textDecoration:'none', color:'inherit'}}>
                    <FormatListBulletedIcon />
                    <div>Portfolio</div>
                </Link>
            </div>
            <div className={ path === '/chats' ? 'footerOption active' : 'footerOption'}>
                <Link to={ username ? '/chats' : '/signin'} style={{textDecoration:'none', color:'inherit'}}>
                    <HubIcon />
                    <div>Network</div>
                </Link>
            </div>
            {/* <div className={ path === '/signin' || path === '/register' || path === '/profile' ? 'footerOption active' : 'footerOption'}>
                <Link to={ username ? '/profile' : '/signin'} style={{textDecoration:'none', color:'inherit'}}>
                    <PeopleIcon />
                    <div>Forum</div>
                </Link>
            </div> */}
        </div>
    </>
    
  )
}
