import React from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';
import './newsDetail.css'
import { ARTICLEDETAIL_QUERY } from './gql/query';
import { useQuery } from '@apollo/client';

export const NewsDetail = () => {
  const { id } = useParams();
  const {data, loading, error} = useQuery(ARTICLEDETAIL_QUERY, {variables: {id}});
  const navigate = useNavigate()

  if(loading) return <div>loading...</div>
  if(error) return <div>Error fetching article</div>

  return (
    <>
        <div className='newsDetail'>

            <div className="title-section">
                <div className="community-sidebar-btn" onClick={() => navigate(-1)}>
                    <div className="btn-icon">
                        <KeyboardBackspaceIcon />
                    </div>
                </div>
                <div className="title">{data.getArticle.title}</div>
            </div>
            

            
            <div className="newsPostImg">
                <img src={data.getArticle.image} alt='Article' />
            </div>

            <div className="newsPostDetail">{data.getArticle.detail}</div>

        </div>
    </>
  )
}
